import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { CustomCenterHttpService } from '../../http/custom-center.http.service';
import * as $ from 'jquery';
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import { API } from 'app/pages/tzb/custom/http/custom-view/custom-view.constant';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';

@Component({
    selector: "business-information",
    templateUrl: './business-information.component.html',
    styleUrls: ['./business-information.component.scss'],
    providers: [CustomCenterHttpService, CusOperationHttpService]
})

export class BusinessInformation {
    TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
    getCustObj: any = {};
    msgs: Message[] = [];
    resultList: any = {};
    dataList: any = {};
    tableList: any = [];
    manageId: string;
    tapTip_q: any;
    code: any;
    cert_type: any = [];
    applyerType: any = [];
    userId: string;

    inValueReportId: string;
    reportCustNm: string;
    // userId: string;
    reportDisplay: boolean = false;
    progress: boolean = false;
    certType: string;
    certID: string;
    marketingObjectType: any = [];
    taskType: any = [];
    dataMapParamIn: any;
    creditSingleDetailList: any = {};
    repayTypeOptions: any[] = [];//还款方式
    guaranteeModeOptions: any[] = [];//担保方式
    countInterrstFlagOptions: any[] = [];//计息标志
    htzl: any[] = [];//

    constructor(
        public activatedRoute: ActivatedRoute,
        public customCenterHttpService: CustomCenterHttpService,
        private commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public httpServices: CusViewHttpService,
        public commonHttpService: CommonHttpService,
        public router: Router,
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.cert_type = this.code['CERT_TYPE'];
        this.applyerType = this.code['applyerType'];
        this.marketingObjectType = this.code['marketingObjectType'];
        this.taskType = this.code['DXRASKTYPE'];
        this.repayTypeOptions = this.code['RepayType'];
        this.guaranteeModeOptions = this.code['GuaranteeModeCustRequest'];
        this.countInterrstFlagOptions = this.code['CountInterrstFlag'];
        this.htzl = this.code['Htzl'];

        this.resultList = JSON.parse(JSON.stringify(this.activatedRoute.snapshot.params));
        if (this.commfunc.getSessionDataCH('distributeParams')) {
            this.dataMapParamIn = JSON.parse(this.commfunc.getSessionDataCH('distributeParams'));
        }
        if (this.dataMapParamIn &&
            this.dataMapParamIn != '' &&
            this.dataMapParamIn != undefined &&
            this.dataMapParamIn != null &&
            this.dataMapParamIn != 'undefined') {
            this.dataList = this.dataMapParamIn;
        }
        console.log("as")
        // dataSource=='01' 或者 dataSource=='02' 时，查询接口，数据实时更新，'03' 时从dataMapParam中取；
        if (this.resultList.dataSource == '01' || this.resultList.dataSource == '02') {
            let infoBean = {
                dataSource: this.resultList.dataSource, // 数据来源
                requestSeqNo: this.resultList.requestSeqNo, // 请求流水ID
                workEffortId: this.resultList.workEffortId, //工作任务编号
            }
            this.httpServices.queryCustomerInfoByWorkEffCondition(infoBean).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.workEffortList && data.workEffortList.length > 0) {
                        this.dataList['fieldList'] = data.workEffortList[0].fieldList;
                        if (this.dataList.dataSource == '01') {
                            this.tableList = this.dataList['fieldList'];
                        } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '10') {
                            // this.getCustObj = this.dataList['fieldList'][0];
                            // 从后端获取数据
                            this.getCustData(this.dataList.custRequestId)
                            this.tapTip()
                        } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '11') {
                            this.getCustObj = this.dataList['fieldList'][0];
                            this.tapTip()
                        }
                        this.queryBasicInfo();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            })
        } else if (this.resultList.dataSource == '03') {
            if (this.dataList.dataSource == '01') {
                this.tableList = this.dataList['fieldList'];
            } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '10') {
                this.getCustObj = this.dataList['fieldList'][0];
                this.tapTip()
            } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '11') {
                this.getCustObj = this.dataList['fieldList'][0];
                this.tapTip()
            }
            this.queryBasicInfo();
        } else if (this.resultList.dataSource == '04') {
            this.queryBatchPreCreditSingleDetailInfo();
        }


    }

    ngOnInit() {

    }

    queryBasicInfo() {
        // 查询证件类型和证件号码
        this.httpService.selectCustBriefInfo({ custNo: this.dataList['custNo'] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.certType = data.idenType;
                this.certID = data.idenNum;
            }
        });
    }

    // 批量预授信单个查询明细信息
    queryBatchPreCreditSingleDetailInfo() {
        let param = {
            serialNo: this.dataList['serialNo'], // 批量预授信明细编号
            pageNum: 1, //明细信息当前页
            pageSize: 10, //明细信息每页记录数
        }
        this.httpService.queryBatchPreCreditSingleDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.resultList && data.resultList.length > 0) {
                    this.creditSingleDetailList = data.resultList[0];
                    if (this.creditSingleDetailList.loanOfficerAdvises) {
                        this.creditSingleDetailList.loanOfficerAdvises = this.creditSingleDetailList.loanOfficerAdvises * 10000;
                    }
                    if (this.creditSingleDetailList.loanSystemAdvises) {
                        this.creditSingleDetailList.loanSystemAdvises = this.creditSingleDetailList.loanSystemAdvises * 10000;
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    //特殊提示
    tapTip() {
        let Certtype = '';
        let CertId = '';
        if (this.dataList['fieldList'][0]['idType']) {
            Certtype = this.dataList['fieldList'][0]['idType']
        } else if (this.dataList['fieldList'][0]['globalType']) {
            Certtype = this.dataList['fieldList'][0]['globalType']
        }
        if (this.dataList['fieldList'][0]['globalId']) {
            CertId = this.dataList['fieldList'][0]['globalId']
        } else if (this.dataList['fieldList'][0]['idNo']) {
            CertId = this.dataList['fieldList'][0]['idNo']
        }

        let param = {
            custName: this.dataList['fieldList'][0]['custName'],
            idType: Certtype,
            idNo: CertId,
            phoneNo: this.getCustObj.custPhone,
            cardNo: this.getCustObj.cardNo,
        }
        this.customCenterHttpService.selectVerifyHistory(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.tapTip_q = data
            }
        })
    }

    //待受理名单
    waitList() {
        let param = {
            deId: this.dataList['deId'],
            dedId: this.dataList['dedId']
        }
        this.customCenterHttpService.queryCheckCustomer(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.attrvaluelist) {
                    this.tableList = data.attrvaluelist
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    //自助渠道贷款
    loans() {
        let param = {
            custRequestId: this.dataList['servSeqNo']
        }
        this.customCenterHttpService.getCustRequestInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.attrvaluelist) {
                    this.getCustObj = data
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 自主渠道贷款
    getCustData(custRequestId) {
        let Postparam = {
            custRequestId: custRequestId,//申请编号
        }
        this.commonHttpService.getCustRequestInfo(Postparam).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.getCustObj = data
                // if (data.postList && data.postList.length > 0) {
                //     this.getCustObj = data
                // }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    //主联系人
    checkPerson() {
        this.customCenterHttpService.findUser({ custNo: this.dataList.custNo }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.infoList != null) {
                    if (data.infoList.length > 0) {
                        this.manageId = data.infoList[0].manageId;
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 查看BI报告
    lookReport() {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        let param = { custNo: this.dataList['custNo'] }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
                        _self.creatMainMsg({ custNo: _self.dataList['custNo'], modNum: data.updatedNum, custName: _self.dataList['custName'], qryUserId: _self.userId, cretType: _self.certType, cretNo: _self.certID, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
                    } else {
                        _self.inValueReportId = data.reportId;
                        _self.reportCustNm = _self.dataList['custNo'];
                        _self.reportDisplay = true;
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            },
            error: function (err) {
                console.log(err);
            }
        })

    }

    // 生成BI报告
    creatMainMsg(param) {
        this.progress = true;
        let params = this.commfunc.handleParams(param);
        this.httpServices.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = data.reportId;
                this.reportCustNm = this.dataList['custNo'];
                this.progress = false;
                this.reportDisplay = true;
            } else {
                this.progress = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.progress = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    // 第三方评价结果
    toThreeView() {
        this.router.navigate(['/pages/tzb/custom/custom-group/third-party-evaluation/custom-evaluate', {yushouxin:'1', groupId: this.creditSingleDetailList.groupId, custNo: this.creditSingleDetailList.custNo, doorId: null, custGroupView: 1, pageNum: 1, groupName: this.creditSingleDetailList.groupName }]);
    }
}